import { AuthenticatedReq } from "Utils/Axios";

export default class WASettingsApi {
  public static getWABasicSettings = async () => {
    return await AuthenticatedReq(`/whatsapp/business-profile`, "GET");
  };

  public static saveWABasicSettings = async (data: any) => {
    return await AuthenticatedReq(`/whatsapp/profile-update`, "POST", { data });
  };

  public static getWAChatBots = async () => {
    return await AuthenticatedReq(`/chatbots`, "GET");
  };
}
