import { ChangeEvent, FC, ReactNode } from "react";
import { Control, Controller, UseControllerProps } from "react-hook-form";
import { Box, CardMedia, Input, Typography } from "@mui/material";

interface FormFileFieldProps extends UseControllerProps {
  control: Control<any, any>;
  defaultValue?: string;
  onChange: (event: ChangeEvent<HTMLInputElement>) => void;
  rules?: any;
  name: string;
  accept?: string;
  allowedTypes?: string;
  previewAlt?: string;
  label: string;
  width?: number;
  height?: number;
  children?: ReactNode;
}

const FormFileField: FC<FormFileFieldProps> = ({
  control,
  defaultValue,
  onChange,
  rules,
  name,
  accept = "image/png, image/jpeg",
  allowedTypes = "png or jpg format",
  previewAlt = "Profile Image",
  label,
  width,
  height,
  children,
  ...props
}) => (
  <Controller
    rules={rules}
    control={control}
    name={name}
    render={({ field }) => (
      <Box
        sx={{
          display: "flex",
          position: "relative",
          margin: "auto",
          transition: "all 0.2s ease-in-out",
          width: width ? width : { xs: 157, lg: 180, xl: 200 },
          height: height ? height : { xs: 157, lg: 180, xl: 200 },
        }}
      >
        {defaultValue && defaultValue !== "" && (
          <Box
            sx={{
              backgroundColor: "#F2F3F7",
              borderRadius: "15px",
              boxShadow: "0px 0px 5px 1px rgba(0, 0, 0, 0.3)",
              display: "flex",
              justifyContent: "center",
              height: "100%",
              left: 0,
              overflow: "hidden",
              pointerEvents: "none",
              position: "absolute",
              top: 0,
              width: "100%",
              zIndex: 2,
            }}
          >
            <CardMedia component="img" image={defaultValue} alt={previewAlt} />
          </Box>
        )}
        <Box
          sx={{
            alignItems: "center",
            backgroundColor: "#F2F3F7",
            borderRadius: "15px",
            display: "flex",
            height: "100%",
            justifyContent: "center",
            flexDirection: "column",
            position: "relative",
            textAlign: "center",
            width: "100%",
          }}
        >
          <Input
            {...props}
            inputProps={{
              accept,
              type: "file",
              title: previewAlt,
              sx: {
                backgroundColor: "transparent",
                borderRadius: "5px",
                height: "100%",
                margin: 0,
                opacity: 0,
                padding: 0,
                position: "absolute",
                top: 0,
                width: "100%",
                zIndex: 0,
              },
            }}
            name={field.name}
            onChange={onChange}
            sx={{
              height: "100%",
              position: "absolute",
              left: 0,
              m: 0,
              opacity: 0,
              p: 0,
              top: 0,
              width: "100%",
              zIndex: 1,
            }}
          />
          <Typography
            sx={{
              color: "#616C7D",
              fontSize: 15,
              fontWeight: 500,
              lineHeight: "18px",
              maxWidth: width,
            }}
          >
            {label}
          </Typography>
          <Typography
            className="allowed-types"
            sx={{
              color: "#758399",
              fontSize: 10,
              fontWeight: 400,
              lineHeight: "12px",
              marginBottom: 0,
            }}
          >
            {allowedTypes}
          </Typography>
        </Box>
      </Box>
    )}
  />
);

export default FormFileField;
